:root {
  --main-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

* {
  box-sizing: border-box;
}

.header__logo {
  vertical-align: bottom;
  width: 40px;
  height: 40px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-align: center;
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--main-color);
}

.is-active {
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

